@font-face {
  font-family: 'Thin';
  src: url('./fonts/thin.otf');
}

.App {
  margin: 0 auto;
  width: 30%;
  background-color: #f5f5f5;
}
h1 {
  font-weight: 100;
  margin-left: 35%;
  font-size: 60px;
  color: #e9d8d8;
  margin-top: 30px;
  padding-bottom: 30px;
}

* {
  margin: 0;
  padding: 0;
  font-weight: 100;
  font-family: 'Thin';
}

p.username {
  margin-left: 12px;
}

span.logout {
  text-decoration: underline;
  font-size: larger;
  float: right;
  margin-right: 15px;
  cursor: pointer;
}
